import { getMyImages, getPublishImages } from "@/api";
import store from "@/state/store";
import { UploadOutlined } from "@ant-design/icons";
import {
  Button,
  Empty,
  Spin,
  Tabs,
  TabsProps,
  Upload,
  UploadProps,
  message,
} from "antd";
import { useEffect, useState } from "react";
import styles from "./index.less";
import config from "@/config";
import OperationMenu from "./OperationMenu";

interface MyMaterialProps {
  // 是否为嵌入到其他页面
  isFrame?: boolean;
}

const MyMaterial: React.FC<MyMaterialProps> = ({ isFrame = false }) => {
  const [imageList, setImageList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentId, setCurrentId] = useState("");
  const [menuShow, setMenuShow] = useState(false);
  const [menuPosition, setMenuPosition] = useState({
    left: 0,
    top: 0,
  });
  const [currentTab, setCurrentTab] = useState("my");

  const props: UploadProps = {
    name: "file",
    action: config.uploadUrl,
    headers: {
      authorization: store.getState().user.access_token,
    },
    showUploadList: false,
    onChange(info) {
      setLoading(true);
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        message.success(`${info.file.name} 上传成功`);
        getList();
      } else if (info.file.status === "error") {
        message.error(`${info.file.name} 上传失败`);
        setLoading(false);
      }
    },
  };

  const getList = async (key: string = "my") => {
    try {
      setLoading(true);
      let res;
      if (key === "my") {
        res = await getMyImages();
      } else {
        res = await getPublishImages();
      }
      setImageList(res?.result);
      setLoading(false);
    } catch (e) {
      message.error("获取数据失败");
      setLoading(false);
    }
  };

  useEffect(() => {
    getList();
  }, []);

  const handleDragStart = (e: any) => {
    e.dataTransfer.setData("url", e.target.dataset.url);
  };

  // 处理右键菜单
  const handleContextMenu = (e: any) => {
    e.stopPropagation();
    e.preventDefault();
    setMenuPosition({
      top: e.clientY,
      left: e.clientX,
    });
    setCurrentId(e.target.dataset.id);
    setMenuShow(true);
  };

  // 点击事件
  const handleClick = (e: any) => {
    if (e.button !== 2 && menuShow) {
      setMenuShow(false);
    }
  };

  const items: TabsProps["items"] = [
    {
      key: "my",
      label: `我的素材`,
    },
    {
      key: "all",
      label: `素材广场`,
    },
  ];

  const handleChange = (key: string) => {
    setMenuShow(false);
    setCurrentTab(key);
    getList(key);
  };

  return (
    <>
      <Tabs defaultActiveKey="my" items={items} onChange={handleChange} />
      <Spin spinning={loading} style={{ height: "100%" }}>
        <>
          {imageList.length ? (
            <div
              onClick={handleClick}
              style={{ overflow: "auto", height: "100%" }}
            >
              {currentTab === "my" && (
                <Upload {...props}>
                  <Button type="primary" icon={<UploadOutlined />}>
                    上传图片
                  </Button>
                </Upload>
              )}
              <div style={{ display: "flex", flexWrap: "wrap", marginTop: 12 }}>
                <div
                  className={isFrame ? styles.frameContainer : styles.container}
                  onDragStart={handleDragStart}
                >
                  {imageList.map((item: any) => {
                    return (
                      <div
                        className={styles.card}
                        key={item.url}
                        draggable
                        data-url={item.url}
                        data-id={item._id}
                        onContextMenu={handleContextMenu}
                      >
                        <img
                          data-id={item._id}
                          draggable={false}
                          src={item.url}
                          style={{ width: "100%", height: "100%" }}
                        />
                      </div>
                    );
                  })}
                  {/* 右击菜单 */}
                  {menuShow && (
                    <OperationMenu
                      btnList={
                        currentTab === "my" ? ["delete", "publish"] : ["add"]
                      }
                      menuPosition={menuPosition}
                      id={currentId}
                      refresh={getList}
                    />
                  )}
                </div>
              </div>
            </div>
          ) : (
            <Empty style={{ marginTop: 12 }} />
          )}
        </>
      </Spin>
    </>
  );
};

export default MyMaterial;
