import styles from "./index.less";
import { addImageToMy, deleteImage, publishImage } from "@/api";
import { message } from "antd";

export type OperationBtnType = "delete" | "publish" | "add";

interface OperationMenuProps {
  menuPosition: {
    left: number;
    top: number;
  };
  id: string;
  btnList: OperationBtnType[];
  refresh?: () => void;
}

const OperationMenu: React.FC<OperationMenuProps> = function ({
  menuPosition,
  id,
  btnList,
  refresh,
}) {
  const handlePublishImage = async () => {
    try {
      await publishImage({ id });
      message.success("发布图片成功");
    } catch (err) {
      console.log(err);
      message.error("发布图片失败");
    }
  };

  const handleDelete = async () => {
    try {
      await deleteImage({ id });
      message.success("删除图片成功");
      refresh?.();
    } catch (err) {
      console.log(err);
      message.error("删除图片失败");
    }
  };

  const handleAddToMy = async () => {
    try {
      await addImageToMy({ id });
      message.success("添加成功");
      refresh?.();
    } catch (err) {
      console.log(err);
      message.error("添加失败");
    }
  };

  return (
    <div
      className={styles.contextmenu}
      style={{
        top: menuPosition.top,
        left: menuPosition.left,
      }}
    >
      <ul>
        <>
          {btnList.includes("publish") && (
            <li onClick={handlePublishImage}>发布</li>
          )}
          {btnList.includes("add") && (
            <li onClick={handleAddToMy}>添加至我的素材</li>
          )}{" "}
          {btnList.includes("delete") && (
            <li onClick={handleDelete}>删除素材</li>
          )}
        </>
      </ul>
    </div>
  );
};

export default OperationMenu;
